<template>
  <div class="box3">
    <input type="button" class="pad" v-on:click="padclick(1)" value="1" />
    <input type="button" class="pad" v-on:click="padclick(2)" value="2" />
    <input type="button" class="pad" v-on:click="padclick(3)" value="3" />
    <input type="button" class="pad" v-on:click="padclick('del')" value="del" />
    <input type="button" class="pad" v-on:click="padclick(4)" value="4" />
    <input type="button" class="pad" v-on:click="padclick(5)" value="5" />
    <input type="button" class="pad" v-on:click="padclick(6)" value="6" />
    <input type="button" class="pad" v-on:click="padclick('esc')" value="esc" />
    <input type="button" class="pad" v-on:click="padclick(7)" value="7" />
    <input type="button" class="pad" v-on:click="padclick(8)" value="8" />
    <input type="button" class="pad" v-on:click="padclick(9)" value="9" />
    <input type="button" class="pad" value=' '/> 
  </div>
</template>

<script>
export default {
  name: "Pad1",
  data() {
    return {};
  },
  created() {},
  props: {},
  methods: {
    padclick: function(v) {
      let i = this.$store.state.a.v_i;
      console.log("pc", v, i);
      let t = this.$store.state.a.valt[i];
      if (t == "f") return; // ignore if fixed
      if (v == "del") {
        v = "";
        this.$store.commit("a/setva", { i: i, e: "" });
      }
      if (v == "esc") {
        this.$store.commit("a/seti", "");
        return;
      }
      if (i !== "") this.$store.commit("a/setva", { i: i, v: v, r: 1 });
    }
  }
};
</script>
